import Vue from 'vue'
import * as type from "../store/type.js"
import Store from "../store/index.js"
import axios from 'axios'




Vue.prototype.$http=axios;

export default {
    /**
     * sessionStorage 
     * @param name
     * @param value
     * @returns {Boolean}
     */
    setSession(name, value) {
        if (!name) return;
        if (Object.prototype.toString.call(value) !== '[object String]') {
            value = JSON.stringify(value);
        }
        window.sessionStorage.setItem(name, value);
    },
    /**
     * sessionStorage 
     * @param name
     * @returns
     */
    getSession(name) {
        if (!name) return;
        var value = window.sessionStorage.getItem(name);
        return JSON.parse(value);
    },
    /**
     * localStorage 
     * @param name
     * @returns
     */
    setLocal(name, value) {
        if (!name) return;
        if (Object.prototype.toString.call(value) !== '[object String]') {
            value = JSON.stringify(value);
        }
        window.localStorage.setItem(name, value);
    },
    /**
     * localStorage
     * @param name
     */
    getLocal(name) {
        if (!name) return;
        var value = window.localStorage.getItem(name);
        if (Object.prototype.toString.call(value) !== '[object String]') {
            return JSON.parse(value);
        } else {
            return value;
        }

    },
    /**
     * @param time
     */
    getTime(time) {
        return time.split(" ")[0]
    },

    /**
     * 
     * @param  s 
     * @param true(),flase()
     */
    isEmpty(s) {
        return s == null || typeof (s) == "undefined" || "null" == s || "undefined" == s || "" == ("" + s).replace(/^\s+|\s+$/g, "");
    },

    /**
     * 
     * @param s 
     * @param true(),flase()
     */
    isEmptyObject(s) {
        var e;
        for (e in s) {
            return false
        }
        return true
    },

    /**
     * 
     * @param 
     */
    ArrMax(arr){
        var num = arr[0];
        for(var i=0;i<arr.length;i++){
            if(num < arr[i]){
                num = arr[i]
            }
        }
        return num;
    },
    
    /**
     * 
     * @param  time 
     * @param {} format 
     * @param yyyy()  MM() dd() HH() mm() ss()
     * @param {} format 'yyyy-MM-dd HH:mm:ss' || 'yyyyMMdd HHmmss' || 'yyyyMMdd' || 'dd/MM/yyyy' || 'yyyyM' || 'Md'
     * @param string '2018-3-22 20:43:26''2018322204346' .....
     */
    timeTostring(time, format) {
        typeof time == 'string' ? time.length == 10 ? time = Number(time) * 1000 : time = Number(time) : '';
        format = format || 'yyyy-MM-dd HH:mm:ss';
        var date = new Date(time);
        var new_date = format.replace(/yyyy/, date.getFullYear())
            .replace(/MM/, date.getMonth() + 1 < 10 ? '0' + String(Number(date.getMonth()) + 1) : String(Number(date.getMonth()) + 1))
            .replace(/dd/, date.getDate() < 10 ? '0' + date.getDate() : date.getDate())
            .replace(/HH/, date.getHours() < 10 ? '0' + date.getHours() : date.getHours())
            .replace(/mm/, date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes())
            .replace(/ss/, date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds());
        return new_date;
    },

    /**
    * 0
    * @param  str 
    */
    substrZero(str) {
        if (str.substr(0, 1) == '0') {
            str = str.substr(1)
        } else {
            str = str;
        }
        return str;
    },
    
    /**
     * @param 0true
     */
    isNumber(val){
        var regPos = /^\d+(\.\d+)?$/; //
        var regNeg = /^(-(([0-9]+\.[0-9]*[1-9][0-9]*)|([0-9]*[1-9][0-9]*\.[0-9]+)|([0-9]*[1-9][0-9]*)))$/; //
        if(regPos.test(val) || regNeg.test(val)){
            return true;
        }else{
            return false;
        }
    },
    
    /**
     * @param number 
     * @param n 
     */
    getFloat(number, n) {
        n = n ? parseInt(n) : 0;
        if (n <= 0) return Math.round(number);
        number = Math.round(number * Math.pow(10, n)) / Math.pow(10, n);
        return number;
    },

    /**
     * @param 
     * @param data 
     * @param type 
     */
    getDataType(data){
        let gettype=Object.prototype.toString,type='';
        if(gettype.call(data)=="[object Object]"){
            type = 'obj';
        }else if(gettype.call(data)=="[object Array]"){
            type = 'arr';
        }else if(gettype.call(data)=="[object String]"){
            type = 'str';
        }
        return type
    },

    // 
    isPrimeNum (number) {
        var flag = true;
        if (number) {
            number = parseInt(number);
        } else {
            flag = false;
            number = 0;
        }
        for (var i = 2; i <= Math.sqrt(number); i++) {
            if (number % i === 0) {
                flag = false;
                break;
            }
        }

        return flag;
    },

    // 
    getRandomNum (count,num) {
        var arr = [];
        for(var i = 0;i<count;i++){
            if(arr.length<count || arr.indexOf(Math.round(Math.random() * num))==-1){
                arr.push(Math.round(Math.random() * (num-1)));
            }
        }
        return arr;
    },

    // uuid
    getuuid(len, radix) {
        var chars = '0123456789ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz'.split('');
        var uuid = [], i;
        radix = radix || chars.length;
     
        if (len) {
          // Compact form
          for (i = 0; i < len; i++) uuid[i] = chars[0 | Math.random()*radix];
        } else {
          // rfc4122, version 4 form
          var r;
     
          // rfc4122 requires these characters
          uuid[8] = uuid[13] = uuid[18] = uuid[23] = '-';
          uuid[14] = '4';
     
          // Fill in random data.  At i==19 set the high bits of clock sequence as
          // per rfc4122, sec. 4.1.5
          for (i = 0; i < 36; i++) {
            if (!uuid[i]) {
              r = 0 | Math.random()*16;
              uuid[i] = chars[(i == 19) ? (r & 0x3) | 0x8 : r];
            }
          }
        }
     
        return uuid.join('');
    },
    //
    deepCopy (source) { 
        var result={};
        for (var key in source) {
                result[key] = typeof source[key]==='object' ? this.deepCopy(source[key]): source[key];
           } 
        return result; 
    },

    //
    reverse(a){
        var len = a.length;//
        var mid = parseInt(len/2);//
        //mid+1
        for(var i = 0;i <= mid;i++){
            var temp = a[i];
            a[i] = a[len-1-i];
            a[len-1-i] = temp;  
        }
        return a;
    },

    //
    avg(num){
        num = num.toFixed(2); 
        num = num.substr(0, num.length-1); 
     var lastStr = num.charAt(num.length-1); 
     if(lastStr%2 == 0){ 
     return num; 
     } else { 
     return(num-0.1).toFixed(1); 
     }
    },

    numString(n){
        return ['one', 'two', 'three', 'four', 'five', 'six'][n-1]
    },
    //
    arrRecord(num){
        let lang = JSON.parse(window.sessionStorage.getItem('langChange'));
        var c ='',d = 0,odd = '',all='';
        for(let i = 0;i<num.length;i++){
            c += '<b class="tou-' + num[i] + '"></b>',d += parseInt(num[i]);
        }
        if(lang){
            d%2==0?odd = "":odd = "";
            d>9?(all = '',d=d):(all ="",d='0'+d);
        }else{
            d%2==0?odd = "Double":odd = "Single";
            d>9?(all = 'Big',d=d):(all ="Small",d='0'+d);
        }
        return c + ('<i>'+all + odd + d + "</i>");
    },
    // lotteryRecord(num){
    //     var num  = num.split(","),c ='',d = 0,odd = '',all='';
    //     for(let i = 0;i<num.length;i++){
    //         c += '<b class="tou-' + num[i] + '"></b>',d += parseInt(num[i]);
    //     }
    //     d%2==0?odd = "":odd = "";
    //     d>9?(all = '',d=d):(all ="",d='0'+d);
    //     return c + ('<i>'+all + odd + d + "</i>");
    // },
}



